/*----------- Variable colors -------------*/

@import url("../../static/Themes.css");

/*------- Contact Form -------*/

.contact__form__container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact__container {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 100px 0;
}

.contact__box__con {
  position: absolute;
}

.contact__box {
  display: flex;
  position: relative;
  top: 100px;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 800px;
  height: auto;
  background-color: var(--light-dark-secondary-color);
  border-radius: 10px;
  margin: 0 auto;
  margin-top: 50px;
  margin-bottom: 50px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 30px;
}

.contact__box__item {
  width: 50%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact__box__item__title {
  font-size: 32px;
  font-weight: 800;
  color: var(--dark-color);
  margin-bottom: 20px;
}

/*------- button -------*/

.contact__box__item a {
  position: relative;
  left: 25px;
  top: 10px;
}

.button {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  border: none;
  background: none;
  color: var(--dark-blue-color);
  cursor: pointer;
  position: relative;
  padding: 8px;
  margin-bottom: 20px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  transition: all 0.15s ease;
}

.button::before,
.button::after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  left: 0;
  height: calc(50% - 5px);
  border: 1px solid var(--light-color);
  transition: all 0.15s ease;
}

.button::before {
  top: 0;
  border-bottom-width: 0;
}

.button::after {
  bottom: 0;
  border-top-width: 0;
}

.button:active,
.button:focus {
  outline: none;
}

.button:active::before,
.button:active::after {
  right: 3px;
  left: 3px;
}

.button:active::before {
  top: 3px;
}

.button:active::after {
  bottom: 3px;
}

.button_lg {
  position: relative;
  display: block;
  padding: 10px 20px;
  color: var(--white-color);
  background-color: #0f1923;
  overflow: hidden;
  box-shadow: inset 0px 0px 0px 1px transparent;
}

.button_lg::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 2px;
  height: 2px;
  background-color: var(--dark-blue-color);
}

.button_lg::after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
  width: 4px;
  height: 4px;
  background-color: var(--dark-blue-color);
  transition: all 0.2s ease;
}

.button_sl {
  display: block;
  position: absolute;
  top: 0;
  bottom: -1px;
  left: -8px;
  width: 0;
  background-color: var(--orange-color);
  transform: skew(-15deg);
  transition: all 0.2s ease;
}

.button_text {
  position: relative;
}

.button:hover {
  color: var(--dark-blue-color);
}

.button:hover .button_sl {
  width: calc(100% + 15px);
}

.button:hover .button_lg::after {
  background-color: var(--white-color);
}

/*------- reponsiveness -------*/

@media screen and (max-width: 1024px) {
  .contact__box {
    flex-direction: column;
    width: 80%;
    height: auto;
    padding: 30px;
    margin-bottom: 70px;
  }
  .contact__box__item {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .contact__box__item__title {
    font-size: 33px;
    font-weight: 700;
    color: var(--dark-color);
    margin-bottom: 20px;
  }
  .contact__box__item a {
    position: relative;
    left: 0px;
    top: 0px;
    margin-top: 10px;
  }
}

@media screen and (max-width: 768px) {
  .contact__box {
    flex-direction: column;
    width: 80%;
    height: auto;
    padding: 30px;
    top: 50px;
  }
  .contact__box__item {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .contact__box__item__title {
    font-size: 33px;
    font-weight: 700;
    color: var(--dark-color);
    margin-bottom: 20px;
  }
  .contact__box__item a {
    position: relative;
    left: 0px;
    top: 0px;
  }
}

@media screen and (max-width: 480px) {
  .contact__box {
    flex-direction: column;
    width: 80%;
    height: auto;
    padding: 30px;
  }
  .contact__box__item {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .contact__box__item__title {
    font-size: 33px;
    font-weight: 700;
    color: var(--dark-color);
    margin-bottom: 20px;
  }
  .contact__box__item a {
    position: relative;
    left: 0px;
    top: 0px;
  }
}
