/*----------- Variable colors -------------*/

@import url("../../static/Themes.css");

/*------- Current_projects -------*/

.current__project {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 0 1rem;
  margin-top: 60px;
}

.Project_intro {
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 0 1rem;
}

.Project_intro h1 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 1rem;
}

.Project_intro p {
  font-weight: 400;
  width: 80%;
  margin-bottom: 1rem;
}

.current__project__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/*------- mobile view -------*/

@media screen and (max-width: 768px) {
  .current__project {
    flex-direction: column;
    padding: 0;
  }
  .Project_intro {
    width: 100%;
    padding: 0;
    align-items: center;
  }
  .Project_intro h1 {
    font-size: 1.5rem;
  }
  .Project_intro p {
    font-size: 0.8rem;
  }
}

/* tablet view */

@media screen and (max-width: 1024px) {
  .Project_intro h1 {
    font-size: 2rem;
  }
  .Project_intro p {
    font-size: 1rem;
  }
}
