/*----------- Variable colors -------------*/

@import url("../../static/Themes.css");

/*-------- Footer section --------*/

.footer__wrapper {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 30px 0;
  background-color: var(--primary-color);
}

.footer__con {
  width: 100%;
  height: auto;
  display: flex;
  padding: 20px 0;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.social__icons {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin-top: 20px;
}

.location__btn {
  padding: 10px;
  border-radius: 10px;
  background-color: var(--white-color);
  color: var(--secondary-color);
  font-weight: 600;
  font-size: 18px;
  border: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  outline: none;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  margin-top: 15px;
}

.location__btn img {
  width: 24px;
}

.left__side {
  width: 100%;
  height: auto;
  color: var(--white-color);
}

.left__side h1 {
  font-size: 30px;
  font-weight: bold;
}

.left__side h4 {
  font-size: 18px;
  font-weight: 600;
  margin-top: 10px;
}

.right__side {
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.right__side img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 50%;
}

/*-------- responsiveness of the screen --------*/

@media screen and (max-width: 768px) {
  .footer__con {
    flex-direction: column-reverse;
  }
  .left__side {
    width: 100%;
    height: auto;
    text-align: center;
    justify-content: center;
  }
  .location__btn {
    margin: 10px auto;
  }
  .social__icons {
    justify-content: center;
  }
}
